}

.result-pane blockquote {
  color: #666;
  margin: 0;
  padding-left: 3em;
  border-left: 0.5em #eee solid;
}

.result-pane tr {
  border-top: 1px solid #c6cbd1;
  background: #fff;
}

.result-pane th,
td {
